.accordeon {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.accordeon-head {
  position: relative;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.accordeon-head svg {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%) rotate(0);
  transition: transform 0.3s linear;
}
.accordeon-head.js-active svg {
  transform: translateY(-50%) rotate(45deg);
}
.accordeon-body {
  display: none;
}
.accordeon-body.js-opened {
  display: block;
}

#chars-accordeon .accordeon-field {
  border-bottom: 1px solid var(--main-border-color);
}
#chars-accordeon .accordeon-head {
  padding: 12px 0;
}
#chars-accordeon .accordeon-body {
  padding-top: 18px;
  padding-bottom: 12px;
  opacity: 0.4;
}
#chars-accordeon .accordeon-body p {
  margin-bottom: 5px;
  max-width: 75%;
}
#chars-accordeon .accordeon-body img {
  margin-top: 10px;
}
#chars-accordeon .accordeon-body.js-opened {
  animation: fadeIn 0.3s ease-in forwards;
}

@keyframes fadeIn {
  0% {
    opacity: 0.4;
  }
  100% {
    opacity: 1;
  }
}
