.form__accept input[type=checkbox] {
  display: none;
}
.form__accept label.checkbox-label {
  position: relative;
  cursor: pointer;
  padding-left: 24px;
  font-size: 14px;
}
.form__accept label.checkbox-label::before {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 16px;
  height: 16px;
  border: 1px solid var(--white-color);
  border-radius: 0;
  background-color: transparent;
  transition: border-color var(--main-transition);
}
.form__accept input[type=checkbox]:checked + label.checkbox-label::before {
  background-image: url("../assets/img/checkbox-icon.svg");
  background-size: 10px;
  background-repeat: no-repeat;
  background-position: center;
}
.form__accept input[type=checkbox].js-invalid-control + label.checkbox-label::before {
  border-color: var(--danger-color);
}
